import { canvasBuilder } from '@eswall/canvas'
import { Application, CommandHistory, Wall } from '@eswall/core'
import {LitElement, css, html} from 'lit'
import {customElement, query, state} from 'lit/decorators.js'
import '@eswall/ui'

@customElement('eswall-container')
export class ESWallContainer extends LitElement {
	static styles = css`
		#container {
			position: relative;
			width: 100vw;
			height: 100vh;
			overflow: hidden;
		}
		ui-toolbar.left {
			position: fixed;
			top: 50%;
			left: 10px;
			transform: translateY(-50%);
		}
		ui-toolbar.top {
			position: fixed;
			left: 50%;
			top: 10px;
			transform: translateX(-50%);
		}
	`

	@query('#container') container !: HTMLDivElement
	@state() app !: Application

	firstUpdated () {
		this.app = new Application(new Wall([]), new CommandHistory(), canvasBuilder(this.container))
	}

	render() {
		const toolbars = html`
			<ui-toolbar class="left">
				<ui-new-note-toolbar-item .app="${this.app}"></ui-new-note-toolbar-item>
			</ui-toolbar>
			<ui-toolbar class="top">
				<ui-undo-toolbar-item .app="${this.app}"></ui-undo-toolbar-item>
				<ui-redo-toolbar-item .app="${this.app}"></ui-redo-toolbar-item>
			</ui-toolbar>
		`
		return html`
			<main>
				<div id="container"></div>
				${ this.app ? toolbars : ``}
			</main>
		`
	}
}
