import { Application } from '@eswall/core'
import {LitElement, css, html, TemplateResult} from 'lit'
import {property, state} from 'lit/decorators.js'
import {classMap} from 'lit/directives/class-map.js'
import {IconName, icons} from './icons'

export abstract class ToolbarItemWithChildrens extends LitElement {
	static styles = [css`
		.wrapper {
			position: relative;
		}
		.item {
			box-sizing: border-box;
			height: 24px;
			width: 24px;
			padding: 3px;
			margin: 0 3px;
			border-radius: 4px;
			cursor: pointer;
		}
		.item:hover {
			background: #BFB78F;
		}
		.item:hover svg {
			fill: #025E73;
		}
		.item svg {
			height: 18px;
			width: 18px;
			fill: #2a2b2b;
		}
		.item.disabled {
			cursor: auto;
		}
		.item.disabled svg {
			fill: #888888;
		}
		.item.disabled:hover svg {
			fill: #888888;
		}
		.subitems {
			display: none;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: calc(100% + 10px);
			border: 1px solid #2a2b2b;
			border-radius: 4px;
			flex-direction: column;
		}
		.subitems.open {
			display: flex;
		}
	`]

	abstract icon : IconName
	abstract clickHandler () : void
	abstract subitems () : TemplateResult

	@property({attribute: false}) app !: Application

	@state() disabled = false
	@state() isOpen = false

	timeout ?: number

	private mouseoutHandler () {
		this.timeout = window.setTimeout(this.closeSubitems.bind(this), 500)
	}

	private mouseoverHandler () {
		if (this.timeout) clearTimeout(this.timeout)
		this.openSubitems()
	}

	private closeSubitems () {
		this.isOpen = false
	}

	private openSubitems () {
		this.isOpen = true
	}

	render() {
		return html`
			<div
				class="wrapper"
				@mouseout="${this.mouseoutHandler}"
				@mouseover="${this.mouseoverHandler}"
			>
				<div
					class="item ${classMap({ disabled: this.disabled })}"
					@click="${this.clickHandler}"
				>
					${icons[this.icon]()}
				</div>
				<div class="subitems ${classMap({ open: this.isOpen })}">
					${this.subitems()}
				</div>
			</div>
		`
	}
}
