import { Application } from "@eswall/core"
import Konva from "konva"
import { KonvaEventObject } from "konva/lib/Node"
import { Vector2d } from "konva/lib/types"
import { Canvas } from "./Canvas"

export class SelectionRectangle extends Konva.Rect {
	private positionStart ?: Vector2d
	private positionEnd ?: Vector2d

	constructor (
        private canvas : Canvas,
		private app: Application
	) {
		super({
			fill: 'rgba(0,0,255,0.3)',
			stroke: 'rgba(0,0,255,0.8)',
			strokeWidth: 1,
			visible: false,
		})

		canvas.addToLayer(this)

		canvas.on('mousedown', this.mouseDownHandler.bind(this))
		canvas.on('mousemove', this.mouseMoveHandler.bind(this))
		canvas.on('mouseup', this.mouseUpHandler.bind(this))

	}

	private mouseDownHandler (event: KonvaEventObject<MouseEvent>) {
		if (event.target !== this.canvas) return

		event.evt.preventDefault()

		this.positionStart = this.canvas.getPointerAbsolutePosition()
		this.positionEnd = this.canvas.getPointerAbsolutePosition()

		this.visible(true)
		this.width(0)
		this.height(0)
	}

	private mouseMoveHandler (event: KonvaEventObject<MouseEvent>) {
		if (!this.visible())  return

		event.evt.preventDefault()

		this.positionEnd = this.canvas.getPointerAbsolutePosition()

		if (!this.positionStart || !this.positionEnd) return

		this.setAttrs({
			x: Math.min(this.positionStart.x, this.positionEnd.x),
			y: Math.min(this.positionStart.y, this.positionEnd.y),
			width: Math.abs(this.positionEnd.x - this.positionStart.x),
			height: Math.abs(this.positionEnd.y - this.positionStart.y)
		})
	}

	private mouseUpHandler (event: KonvaEventObject<MouseEvent>) {
		if (!this.visible()) return

		event.evt.preventDefault()
		event.evt.stopPropagation()

		this.visible(false)

		const rect = this.getClientRect()
		const elementsWithIntersection = this.canvas.elementsManager.findElementsWithIntersection(rect)

		const ids = elementsWithIntersection.map(element => element.getId())
		this.app.selectElementsByIds(ids)
	}
}
