import { HistoryChangedEvent } from '@eswall/core'
import {customElement, state} from 'lit/decorators.js'
import { IconName } from './icons'
import { ToolbarItem } from './ToolbarItem'

@customElement('ui-undo-toolbar-item')
export class UndoToolbarItem extends ToolbarItem {
	icon : IconName = 'undo'

	@state() disabled = true

	firstUpdated () {
		this.app.subscribe(HistoryChangedEvent, this.historyChangeHandler.bind(this))
	}

	private historyChangeHandler () {
		this.disabled = !this.app.isUndoAvailable()
	}

	clickHandler () {
		this.app.undo()
	}
}
