import Konva from "konva"
import { FontSizeCalculator } from "../utils/FontSizeCalculator"
import { Canvas } from "../Canvas"

export class ContentEditableArea extends HTMLDivElement {
	static create (canvas: Canvas, node: Konva.Text) {
		const contentEditableArea = document.createElement('div', { is: 'content-editable-area' }) as ContentEditableArea

		contentEditableArea
			.setWall(canvas)
			.setNode(node)
			.applyText()
			.applyStyles()

		canvas.container().appendChild(contentEditableArea)

		return contentEditableArea
	}

	private canvas !: Canvas
	private node !: Konva.Text
	private calculator !: FontSizeCalculator
	private editableNode : HTMLDivElement
	private outsideClickHandlerFn : (event: Event) => void

	constructor(
	) {
		super()

		this.editableNode = document.createElement('div')
		this.editableNode.setAttribute('contenteditable', 'true')
		this.appendChild(this.editableNode)

		this.outsideClickHandlerFn = this.outsideClickHandler.bind(this)
	}

	setWall (wall: Canvas) {
		this.canvas = wall
		this.canvas.on('move zoom', this.wallChangeHandler.bind(this))
		return this
	}

	setNode (node: Konva.Text) {
		this.node = node
		this.calculator = new FontSizeCalculator(this.node)
		return this
	}

	connectedCallback() {
		this.addEventListener('keydown', this.keydownHandler.bind(this))
		this.addEventListener('input', this.changeHandler.bind(this))
		window.addEventListener('mousedown', this.outsideClickHandlerFn)
		this.setCaretOnTheEndOfDiv()
	}

	remove() {
		this.dispatchEvent(new CustomEvent('beforeRemove'))
		window.removeEventListener('mousedown', this.outsideClickHandlerFn)

		super.remove()
	}

	private wallChangeHandler () {
		this.applyStyles()
	}

	private outsideClickHandler (event: Event) {
		if (event.target !== this && event.target !== this.editableNode) {
			this.dispatchContentChangedEvent()
			this.remove()
		}
	}

	private keydownHandler(e: KeyboardEvent) {
		if (e.key === 'Enter' && !e.shiftKey) {
			this.dispatchContentChangedEvent()
			this.remove()
		}
		else if (e.key === 'Escape') {
			this.remove()
		}
	}

	private changeHandler () {
		this.setMaxFontSize()
	}

	private setMaxFontSize () {
		const fontSize = this.calculator.getMaxFontSize(this.innerText)
		this.style.fontSize = fontSize + 'px'
	}

	private dispatchContentChangedEvent() {
		const event = new CustomEvent('contentChanged', {
			detail: {
				content: this.editableNode.innerText
			}
		})
		this.dispatchEvent(event)
	}

	private setCaretOnTheEndOfDiv() {
		this.editableNode.focus()
		const range = document.createRange()
		range.selectNodeContents(this.editableNode)
		range.collapse(false)
		const selection = window.getSelection()
		selection?.removeAllRanges()
		selection?.addRange(range)
	}

	applyText () {
		this.editableNode.innerText = this.node.text()
		return this
	}

	applyStyles () {
		const textPosition = this.node.absolutePosition()

		this.style.position = 'absolute'
		this.style.top = (this.canvas.container().offsetTop + textPosition.y) + 'px'
		this.style.left = (this.canvas.container().offsetLeft + textPosition.x) + 'px'
		this.style.width = this.node.width() + 'px' || '150px'
		this.style.height = this.node.height() + 'px' || '150px'
		this.style.fontSize = this.node.fontSize() + 'px' || '16px'
		this.style.padding = this.node.padding() + 'px' || '0px'
		this.style.overflow = 'hidden'
		this.style.display = 'flex'
		this.style.alignItems = this.node.align()
		this.style.justifyContent = 'center'
		this.style.wordBreak = 'break-word'
		this.style.lineHeight = this.node.lineHeight().toString() || '1.6'
		this.style.fontFamily = this.node.fontFamily() || ''
		this.style.transformOrigin = 'left top'
		this.style.textAlign = this.node.align()
		this.style.color = this.node.fill()
		this.style.transform = `scale(${this.canvas.scale().x})`

		this.editableNode.style.width = this.node.width() + 'px' || '150px'
		this.editableNode.style.maxHeight = this.node.height() + 'px' || '150px'
		this.editableNode.style.outline = 'none'

		return this
	}
}

customElements.define('content-editable-area', ContentEditableArea, { extends: 'div' })
