import Konva from "konva"
import { IRect } from "konva/lib/types"
import { Canvas } from "../Canvas"

export function getWrapperRect (canvas: Canvas, nodes: Konva.Node[], padding = 0) : IRect {
	let top = 0
	let left = 0
	let right = 0
	let bottom = 0

	nodes.forEach(node => {
		const { x, y, width, height } = node.getClientRect()
		if (!top || (y < top)) top = y
		if (!left || (x < left)) left = x
		if (!right || (x + width > right)) right = x + width
		if (!bottom || (y + height > bottom)) bottom = y + height
	})

	return {
		width: (right - left ) / canvas.scaleX() + padding * 2,
		height: (bottom - top) / canvas.scaleX() + padding * 2,
		x: (left - canvas.x()) / canvas.scaleX() - padding,
		y: (top - canvas.y()) / canvas.scaleX() - padding
	}
}
